.DonationTrackingMainComponants {
    margin: 3em 0 0 0;
    .DonationTrackingComponantsTitle{
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 2em;
        a{

            img{
            width: 50px;
            &:hover{
                cursor: pointer;
            } 
            }
        }
    }

}
.DonationTrackingComponantsTitleHistory{
    margin: 2em 0;
}

.timeline{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.timeline-wrapper {
    position: relative;
    padding: 22px 0;
    display: flex;
    flex-direction: column;
}

.timeline-wrapper:last-child:before {
    height: 0;
}

.timeline-wrapper:before {
    content: "";
    background: #246359;
    width: 3px;
    height: 100%;
    position: absolute;
    left: 38px;
}

.timeline-yr {
    background:  #246359;
    border-radius: 100%;
    position: absolute;
    width: 85px;
    height: 85px;
    line-height: 75px;
    text-align: center;
}

.timeline-yr span {
    color: #ffffff;
    font-size: 16px;
    font-weight: bold;
    display: block;
    line-height: 75px;
}

.timeline-info {
    display: inline-block;
    vertical-align: top;
    max-width: 432px;
    margin-left: 6em;
}

.timeline-info small {
    color: #474559;
    font-size: 16px;
    font-weight: bold;
    display: inline-block;
    vertical-align: middle;
    margin-left: 15px;
}